html {
  font-size: 62.5%;
  box-sizing: border-box;
}

html *,
html *:before,
html *:after {
  box-sizing: inherit;
}

body {
  font-family: "Arial", "Helvetica Neue", Helvetica, serif;
  font-size: 1.2em;
  line-height: 1.6666667;
  min-width: 320px;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Hide visually but not from screen readers */
.hide {
  position: absolute !important;
  left: -9999em !important;
}

/* Image Replacement */
.ir {
  display: block;
  position: relative;
  overflow: hidden;
}

.ir span {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}

/* Acessibility navigation */
.accessibility-nav {
  position: absolute;
  top: 0;
  left: -9999em;
  z-index: 1000;
}

.accessibility-nav a {
  position: absolute;
  top: 0;
  white-space: nowrap;
}

.accessibility-nav a:active,
.accessibility-nav a:focus {
  left: 9999em;
}

/* Footer */
.footer {
  clear: both;
}

